import React, { useState, useContext } from "react";
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Grid,
  IconButton,
  Box,
  Link,
} from "@mui/material";
import { SettingsContext } from "../context/SettingsContext";
import EmailIcon from "@mui/icons-material/Email";
import InstagramIcon from "@mui/icons-material/Instagram";
import FlickrIcon from "@mui/icons-material/PhotoLibrary";
import SchoolIcon from "@mui/icons-material/School";
import LinkIcon from "@mui/icons-material/Link";

const ProfileCard = () => {
  //  useState 
  const [avatar, setAvatar] = useState(require("../assets/img/steven.JPG"));
  const { language } = useContext(SettingsContext); //  Context 

  // 
  const handleAvatarClick = () => {
    setAvatar((prevAvatar) =>
      prevAvatar === require("../assets/img/steven.JPG")
        ? require("../assets/img/steven_cartoon_2.JPG")
        : require("../assets/img/steven.JPG")
    );
  };

  // 
  const name = language === "en" ? "Xi Luo" : "";
  const title = language === "en" ? "Junior Machine Learning Engineer" : "";

  return (
    <Card
      sx={{
        width: 320,
        padding: 2,
        textAlign: "center",
        boxShadow: "none !important", // 
        background: "transparent !important", //  background 
      }}
    >
      {/*  */}
      <CardMedia
        component="img"
        alt={name}
        src={avatar}
        onClick={handleAvatarClick} // 
        sx={{
          width: 200,
          height: 200,
          margin: "0 auto",
          borderRadius: "50%", // 
          cursor: "pointer", // 
        }}
      />
      {/*  */}
      <CardContent>
        <Typography variant="h6" component="div">
          {name}
        </Typography>
        <Typography variant="subtitle1" color="text.secondary">
          {title}
        </Typography>
        <Link
          href="https://addaxis.ai"
          target="_blank" // 
          rel="noopener noreferrer" // 
          color="inherit" // 
          underline="hover" // 
          sx={{ mt: 1, display: "inline-block" }}
        >
          @addaxis.ai
        </Link>
      </CardContent>

      {/*  */}
      <Box>
        <Grid container justifyContent="center" spacing={2}>
          <Grid item>
            <IconButton href="#" color="primary" aria-label="email">
              <EmailIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="#" color="secondary" aria-label="instagram">
              <InstagramIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="#" color="default" aria-label="flickr">
              <FlickrIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="#" color="info" aria-label="google scholar">
              <SchoolIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="#" color="default" aria-label="orcid">
              <LinkIcon />
            </IconButton>
          </Grid>
        </Grid>
      </Box>
    </Card>
  );
};

export default ProfileCard;
